import React, { useState } from 'react';
import {connect} from 'react-redux';
import './App.css';
import * as actionTypes from './store/action/todo';

function App(props) {
  const [value,setvalues]=useState('');
  
  const onSubmitHandler=(e)=>{
    e.preventDefault();
props.onSubmit(value);
  }
  const onDelete=(value)=>{

    props.onDelete(value);
    console.log(value)
  }
const hello=props.sub.map((value,id)=>{
return <p key={id} className="main_paragraph"  onClick={()=>onDelete(id)}>â€¢ {value}</p>
})
  console.log(props.sub);
  return (
    <div className="main_todolist">
      <h1>Todo List</h1>
      <form onSubmit={onSubmitHandler}>
<input type="text" className="main_input" placeholder="i.e meeting at 5 PM" onChange={(event)=>{setvalues(event.target.value)}}></input>
<button type="submit" className="main_button">Add</button>
      </form>
     {hello}
    </div>
  );
}
const mapStateToProsp=state=>{
  return{
    sub:state.arr
  }
}
const mapDispatchProps=dispatch=>{
  return{
    onSubmit:(value)=>dispatch(actionTypes.add(value)),
    onDelete:(value)=>dispatch(actionTypes.deleted(value))
  }
}

export default connect(mapStateToProsp,mapDispatchProps)(App);
